<template lang="pug">
  .page.page-register.color.red
    section.sectionHero
      .container
        router-link.btn-back(to="/rules") 
            i.fa.fa-undo
            span 
        .row
          .col-sm-3
            h3.title-eng.color.white {{ $t('register.engtitle') }}
            h1.title  {{ $t('register.title') }}
            p(v-html=" $t('register.description') ")
            a.btn.white(href="/static/file/-__.pdf", target="_blank") 
              i.fa.fa-file
              span &nbsp;&nbsp;
            //- pre {{registData}}
          .col-sm-9.col-form
            
            ul.navTab
              li(@click="toFormPart(0)",:class="{active: formPart==0}") 1. 
              li(@click="toFormPart(1)",:class="{active: formPart==1}") 2. 
              li(@click="toFormPart(2)",:class="{active: formPart==2}") 3. 
              li(@click="toFormPart(3)",:class="{active: formPart==3}") 4. 

            el-form(label-width="100px", label-position="left", action="http://api.taipeisoundscape.com/registwork2018", method="post",enctype="multipart/form-data")
              .container(v-show="formPart==0")
                .row
                  .col-sm-12
                    label :
                    span
                      .btn(@click="creator_count_change(-1)") -
                      span.number {{registData.personCount}}
                      input(type="hidden",name="creator_count", v-model="registData.personCount")
                      .btn(@click="creator_count_change(1)") +
                    hr
                .row(v-for="(p,pid) in parseInt(registData.personCount)")
                  .col-sm-2
                    h4 {{pid+1}}.<br> {{registData.person[pid].name}}
                  .col-sm-10
                    el-form-item(label="", placeholder="")  
                      el-input(v-model="registData.person[pid].name", :name="'creator_'+pid+'_name'")
                    el-form-item(label="")  
                      el-radio(v-model="registData.person[pid].gender" label="", :name="'creator_'+pid+'_gender'") 
                      el-radio(v-model="registData.person[pid].gender" label="", :name="'creator_'+pid+'_gender'") 
                      el-radio(v-model="registData.person[pid].gender" label="", :name="'creator_'+pid+'_gender'") 
                    el-form-item(label="")  
                      el-input(v-model="registData.person[pid].age",
                                placeholder="18",
                                :name="'creator_'+pid+'_age'",
                                type="number")
                    el-form-item(label="")  
                      el-date(v-model="registData.person[pid].birthday",
                                :name="'creator_'+pid+'_birthday'")
                    el-form-item(label="")   
                      el-input(v-model="registData.person[pid].phone", 
                                placeholder="09xx-xxx-xxx / 02-xxxx-xxxx",
                                :name="'creator_'+pid+'_phone'",)
                    el-form-item(label="")  
                      el-input(v-model="registData.person[pid].mail", placeholder="yourmail@example.com",
                                :name="'creator_'+pid+'_mail'",)
                    el-form-item(label="")  
                      el-input(v-model="registData.person[pid].address", placeholder="e.g. ",
                                :name="'creator_'+pid+'_address'")
                    el-form-item(label="")  
                      label  3 ? &nbsp;&nbsp;&nbsp;
                      el-radio(v-model="registData.person[pid].attend_workshop", :label="true",
                                :name="'creator_'+pid+'_attend_workshop'") 
                      el-radio(v-model="registData.person[pid].attend_workshop", :label="false",
                                :name="'creator_'+pid+'_attend_workshop'") 
                  .col-sm-12
                    hr
                .btn.btn-default.btn-next.red.float-right(@click="nextStep") 

              .container(v-show="formPart==1")
                .row
                  .col-sm-12
                    p  10  1  3 
                div.row(v-for="(c,cid) in parseInt(registData.conceptCount)")
                  .col-sm-12
                    el-form-item(:label="''+ (cid+1)")  
                      el-select(v-model="registData.concepts[cid].target", placeholder="",:name="'concept_'+cid+'_target'")
                        el-option(v-for="op in stationsOptions"
                                  :value="op" ) {{op}}
                    el-form-item(label="", v-if="registData.concepts[cid].target")  
                      el-input(v-model="registData.concepts[cid].content",:name="'concept_'+cid+'_content'"
                                type="textarea", placeholder="100", rows=5)
                    hr
                .btn.btn-default.btn-next.red(@click="prevStep") 
                .btn.btn-default.btn-next.red.float-right(@click="nextStep") 

              .container(v-show="formPart==2")
                .row
                  .col-sm-12
                    ul
                      li ,
                      li  demo ( 10%)
                      li 1-3 ( 3 ), 5 
                      li  MP3192kbps stereo 
                      li  10MB
                .row(v-for="(w,wid) in parseInt(registData.workCount)")
                  .col-sm-2
                    h4 {{wid+1}}.
                  .col-sm-10
                    el-form-item(label="")  
                      el-input(v-model="registData.works[wid].name",:name="'work_'+wid+'_name'")
                    el-form-item(label="")  
                      el-input(v-model="registData.works[wid].content",:name="'work_'+wid+'_content'",type="textarea")
                    el-form-item(label="")  
                      input(type="file",:name="'work_'+wid+'_file'")
                  .col-sm-12
                    hr

                .btn.btn-default.btn-next.red(@click="prevStep") 
                .btn.btn-default.btn-next.red.float-right(@click="nextStep") 


              .container(v-show="formPart==3")
                .row
                  .col-sm-12
                    p 
                    el-form-item(v-for="(p,pid) in parseInt(registData.personCount)",
                                  :label="''+pid+' '+registData.person[pid].name" 
                                  label-width="200px")  
                      input(type="file",:name="'creator_'+pid+'_agreement_file'")
                    hr
                    span  
                      el-checkbox
                    button.btn.red.btn-send(type="submit") 



</template>

<script>
import {mapState,mapMutations,mapActions} from 'vuex'
import $ from 'jquery'
export default {
  data(){
    return {
      formPart: 0,
      registData: {
        personCount: 1,
        conceptCount: 3,
        workCount: 3,
        person: [{},{},{},{},{},{},{},{},{},{}],
        concepts: [{},{},{},{}],
        works: [{},{},{}]
      }
    }
  },
  mounted(){

  },
  computed:{
    ...mapState(['stations']),
    stationsOptions(){
      return this.stations.map(s=>s.name)
    }
  },
  methods:{
    creator_count_change(delta){
        if (this.registData.personCount>=1 && delta>0){
            this.registData.personCount+=delta;
        }else if (this.registData.personCount>1 && delta<0){
            this.registData.personCount+=delta;
        }
    },
    toFormPart(value){
      this.formPart=value
    },
    nextStep(){
      this.formPart=(this.formPart+1)
    },
    prevStep(){
      this.formPart=(this.formPart-1)
    },
    sendData(){
      this.$message("")
      console.log($("form").serialize())
    }
  }
}
</script>

<style lang="sass">
@import "../../assets/_mixins.sass"
.page-register
  // background-image: url(/static/img/ma p-01.svg)
  background-size: contain
  background-position: center center
  background-repeat: no-repeat
  background-attachment: fixed
  .el-form-item
    margin-bottom: 10px
  .navTab
    display: flex
    justify-content: center
    align-items: center
    list-style: none
    padding: 0
    
    li
      border-bottom: solid 10px rgba(white,0.4) 
      flex: 1
      text-align: center
      margin-right: 10px
      cursor: pointer
      font-size: 20px
      font-weight: 600
      +trans
      opacity: 0.6
      &:last-child
        margin-right: 0
      &:hover
        border-bottom: solid 10px rgba(white,0.6)
        opacity: 0.8
      &.active
        border-bottom: solid 10px rgba(white,1)
        opacity: 1
  .el-form
    background-color: $colorWhite
    color: black
    padding: 50px 0px
  .el-select,.el-input,.el-textarea
    width: 100%
  .number
    margin: 10px
    font-size: 20px
  input,textarea
    border: solid 2px #666
    width: 100%
    background-color: transparent
    &:focus
      border: solid 2px $colorRed

  .col-form
    margin-top: 50px
  .btn-send
    display: block
    width: 100%
</style>



